import React from 'react'
import ExpandableInputImpl from './ExpandableInputImpl'

const DSExpandableInput = ({
  containerProps = {},
  innerRef,
  className = '',
  style = {},
  triggerComponent = undefined,
  children,
  isOpen = false,
  onOpen = () => null,
  onClose = () => null,
  onBlur = () => null,
  width = undefined,
}) => {
  return (
    <ExpandableInputImpl
      className={className}
      containerProps={containerProps}
      innerRef={innerRef}
      isOpen={isOpen}
      onBlur={onBlur}
      onClose={onClose}
      onOpen={onOpen}
      style={style}
      triggerComponent={triggerComponent}
      width={width}
    >
      {children}
    </ExpandableInputImpl>
  )
}

export default DSExpandableInput
